import { Injectable } from '@angular/core';
import {HttpClient,HttpHeaders} from '@angular/common/http';
import * as Rx from 'rxjs-compat';
import { from, Observable, throwError } from 'rxjs';
import { map, catchError } from 'rxjs/operators';
import {DietPlanService} from './diet-plan.service';
@Injectable({
  providedIn: 'root'
})
export class HttpService {

  constructor(private http: HttpClient,
              private dietPlanService: DietPlanService) {
  }


  onLogin(data) {
    return this.http.post('https://know-your-calories.herokuapp.com/login', data, {responseType: 'json'}).pipe(
      map((data: any) => {
        return data;
      }), catchError(error => {
        return throwError('Something went wrong!');
      }));
  }

  onSignup(data) {
    return this.http.post('https://know-your-calories.herokuapp.com/signup', data, {responseType: 'text'});
  }

  onSendPlans(data) {
    return this.http.post('https://know-your-calories.herokuapp.com/plans', data, {responseType: 'json'}).pipe(
      map((data: any) => {
        return data;
      }), catchError(error => {
        return throwError('Something went wrong!');
      }));


  }
  onEditName(data){
    return this.http.post('https://know-your-calories.herokuapp.com/editname', data,{responseType: 'text'}).pipe(
      map((data: any) => {
        return data;
      }), catchError(error => {
        return throwError('Something went wrong!');
      }));
  }
  onDeletePlan(data){
    return this.http.post('https://know-your-calories.herokuapp.com/deleteplan', data,{responseType: 'text'}).pipe(
      map((data: any) => {
        return data;
      }), catchError(error => {
        return throwError('Something went wrong!');
      }));
  }

  onSaveRecipeName(data){
    return this.http.post('https://know-your-calories.herokuapp.com/saveRecipeName', data,{responseType: 'text'}).pipe(
      map((data: any) => {
        return data;
      }), catchError(error => {
        return throwError('Something went wrong!');
      }));
  }

  onSendWorkoutPlans(data) {
    return this.http.post('https://know-your-calories.herokuapp.com/workoutPlans', data, {responseType: 'json'}).pipe(
      map((data: any) => {
        return data;
      }), catchError(error => {
        return throwError('Something went wrong!');
      }));


  }
  onDeleteWorkoutPlan(data){
    return this.http.post('https://know-your-calories.herokuapp.com/deleteWorkoutPlan', data,{responseType: 'text'}).pipe(
      map((data: any) => {
        return data;
      }), catchError(error => {
        return throwError('Something went wrong!');
      }));
  }

  onEditWorkoutName(data){
    return this.http.post('https://know-your-calories.herokuapp.com/editWorkoutName', data,{responseType: 'text'}).pipe(
      map((data: any) => {
        return data;
      }), catchError(error => {
        return throwError('Something went wrong!');
      }));
  }

  onGetData(data) {
    let headers   = new HttpHeaders();
    return this.http.post('https://know-your-calories.herokuapp.com/getData', data,{
      headers: new HttpHeaders({'Authorization': `Bearer ${this.dietPlanService.token}`,'responseType': 'json'})}).pipe(
      map((data: any) => {
        return data;
      }), catchError(error => {
        return throwError('Something went wrong!');
      }));
  }


}
